





































dl{
    dt{
        display: flex;
        padding-top: 10px;
        .comment-image{
            // width: 30px;
            margin: 10px;
            img{
                border-radius: 50%;
            }
        }
        .comment-text{
            // padding: 2px ;
            flex: 1;
            margin-top: 4px;
            .nickname{
                
                color: #666;
                line-height: 20px;
                font-size: 14px;
            }
            .time{
                // vertical-align: bottom;
               color: #999;
               font-size: 9px;
            }
        }
        .comment-liked{
            margin-top: 15px;
            margin-right: 8px;
            vertical-align: bottom;
            img{
                width: 20px;
                height: 20px;
                vertical-align: baseline;
            }
        }
    }
    dd{
        // color: red;
        vertical-align: middle;
        color: #333;
        font-size: 15px;
        line-height: 22px;
        margin-left: 50px;
        margin-right: 10px;
        // line-height: 22px;
        border-bottom: 1px solid rgb(246, 242, 242);
        padding-bottom: 10px;
    }
}
