@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.palypage-lyric {
  flex: 1;
  overflow: hidden;
  margin: 30px;
}
.palypage-lyric ul {
  transform: translateY(244px);
  transition: all 0.5s;
}
.palypage-lyric ul li {
  text-align: center;
  color: #e2e2e2;
}
.palypage-lyric ul li span {
  line-height: 2em;
}
.palypage-lyric ul li.active span {
  background: linear-gradient(to right, #52ef57 50%, #e322ef 55%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200%;
  background-position-x: -100%;
  animation: mymove linear;
}
.palypage-lyric ul li.active.paused span {
  animation-play-state: paused;
}
