dl dt {
  display: flex;
  padding-top: 10px;
}
dl dt .comment-image {
  margin: 10px;
}
dl dt .comment-image img {
  border-radius: 50%;
}
dl dt .comment-text {
  flex: 1;
  margin-top: 4px;
}
dl dt .comment-text .nickname {
  color: #666;
  line-height: 20px;
  font-size: 14px;
}
dl dt .comment-text .time {
  color: #999;
  font-size: 9px;
}
dl dt .comment-liked {
  margin-top: 15px;
  margin-right: 8px;
  vertical-align: bottom;
}
dl dt .comment-liked img {
  width: 20px;
  height: 20px;
  vertical-align: baseline;
}
dl dd {
  vertical-align: middle;
  color: #333;
  font-size: 15px;
  line-height: 22px;
  margin-left: 50px;
  margin-right: 10px;
  border-bottom: 1px solid #f6f2f2;
  padding-bottom: 10px;
}
