li.current-play-list-item {
  display: flex;
}
@keyframes shensuo {
  from {
    height: 20%;
  }
  to {
    height: 100%;
  }
}
li.current-play-list-item .palying-animate {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
li.current-play-list-item .palying-animate span {
  width: 20%;
  height: 10%;
  background: green;
  animation: shensuo 0.6s linear infinite alternate;
}
li.current-play-list-item .palying-animate span:nth-child(1) {
  animation-delay: 0s;
}
li.current-play-list-item .palying-animate span:nth-child(2) {
  animation-delay: -0.2s;
}
li.current-play-list-item .palying-animate span:nth-child(3) {
  animation-delay: -0.4s;
}
li.current-play-list-item .palying-animate.paused span {
  animation-play-state: paused;
}
li.current-play-list-item .detail {
  flex: 1;
  border-bottom: 1px solid #dbd8d8;
  padding: 5px;
}
li.current-play-list-item .detail .artist {
  color: #999;
  font-size: 14px;
}
li.current-play-list-item .delete {
  margin-right: 5px;
  color: #999;
}
