.current-palylist {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding-bottom: 5%;
}
.current-palylist .mask {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.current-palylist .card {
  width: 90%;
  min-height: 100px;
  background: white;
  max-height: 60vh;
  border-radius: 10px;
}
.current-palylist h3 {
  padding: 5px;
  font-size: 14px;
  color: #666;
}
