




































li.musiclist-item{
        display: flex;
        align-items: center;
        .index{
            margin-left: 10px;
        }
        &:nth-child(1),
        &:nth-child(2),
        &:nth-child(3){
            .index{
                color: red;
            }
        }
        .info{
            display: flex;
            flex: 1;
            align-items: center;
            border-bottom: 1px solid #ccc;
            padding: 10px 0;
            padding-right: 10px;
            margin-left: 10px;
            .detail{
                flex: 1;
                h3{
                    color: #333;
                    font-size: 17px;
                }
                p{
                    color: #888;
                    font-size: 12px;
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;              
                    .artist{
                        &::after{
                            content: "/";
                        }  
                    &:last-of-type{
                        &::after{
                            content: "";
                        }
                    }
                    }
                    em{
                        font-style: normal;        
                    }
                }
            }
            .icon{
                width: 22px;
                height: 22px;
                background-image: url("../assets/index_icon_2x.png");
                background-size: 166px 89px;
                background-position: -24px 0;
            }
        }
    }
