





























































@keyframes mymove {
  from {
    background-position-x: -100%;
  }
  to {
    background-position-x: -200%;
  }
}
.palypage-lyric {
  flex: 1;
  overflow: hidden;
  margin: 30px;
  ul {
    transform: translateY(244px);
    transition: all 0.5s;
    li {
      text-align: center;
      color: rgb(226, 226, 226);
      span {
        line-height: 2em;
      }
      &.active {
        span {
          background: linear-gradient(to right, #52ef57 50%, #e322ef 55%);
          background-clip: text;
          -webkit-text-fill-color: transparent;
          background-size: 200%;
          background-position-x: -100%;

          animation: mymove linear;
        }
        &.paused {
          span {
            animation-play-state: paused;
          }
        }
      }
    }
  }
}
