














































































.head {
  .head-img {
    height: 200px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.9);
    transform: scale(1.5);
    .head-l{
      height: 200px;
      .head-bottom{
        width: 100%;
        height: 200px;
      }
    }
  }
  .head-i{
    .head-top{
      position: absolute;
    }
  }
  .song{
    color: #999;
    font-size: 14px;
    text-align: center;
    margin: 5px 0;
  }
  .comment{
    padding: 0 10px;
    height: 23px;
    line-height: 23px;
    color: #666;
    font-size: 12px;
    font-weight: 400;
    background: rgba(0,0,0,.05);
  }
  .af{
    text-align: center;
    margin: 5px 0;
}
}
