@keyframes disc-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.disc {
  width: 100%;
  text-align: center;
}
.disc .disc-one {
  border-radius: 50%;
  animation: disc-rotate 10s linear infinite;
  margin: 0 auto;
}
.paused img {
  animation-play-state: paused;
}
