





























































.m-input{ 
  padding: 15px 10px;
  position: relative;
  &::after{
    position: absolute;
    z-index: 2;
    content: "";
    top: 0px;
    left: 0;
    pointer-events: none;
    box-sizing: border-box;
    transform-origin: top left;
    border: 0 solid rgba(0,0,0,.1);
    border-bottom-width: 1px;
    width: 200%;
    height: 200%;
    transform: scale(.5);
  }
.search {
  position: relative;
  width: 100%;
  height: 30px;
  padding: 0 30px;
  box-sizing: border-box;
  background: #ebecec;
  border-radius: 30px;
  margin: 10px auto;

  input {
    width: 100%;
    height: 30px;
    line-height: 18px;
    background: rgba(0, 0, 0, 0);
    font-size: 14px;
    color: #333;
    border: 0;
    outline: 0;
    line-height: 30px;
  }
  .searchicon{
      position: absolute;
    left: 0;
    top: 8px;
    margin: 0 10px;
    vertical-align: middle;
    width: 13px;
    height: 13px;
    background-image:url(../assets/search.png);
    background-size: contain;
    color:#ccc
  }
}
}
