.control .progerss {
  width: 100%;
  height: 20px;
  position: relative;
  display: flex;
  align-items: center;
}
.control .progerss input {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 1;
  opacity: 0;
}
.control .progerss .line {
  background: white;
  width: 100%;
  height: 4px;
  border-radius: 10px;
}
.control .progerss .dot {
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 50%;
  position: absolute;
  top: 5px;
  left: 0;
  margin-left: -5px;
  z-index: 0;
  box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.3);
}
.control .icons {
  margin: 50px 0;
  display: flex;
  justify-content: space-around;
  color: white;
  align-items: center;
}
.control .icons li img {
  width: 30px;
  height: 30px;
}
.control .icons li span img {
  width: 40px;
  height: 40px;
}
