li.musiclist-item {
  display: flex;
  align-items: center;
}
li.musiclist-item .index {
  margin-left: 10px;
}
li.musiclist-item:nth-child(1) .index,
li.musiclist-item:nth-child(2) .index,
li.musiclist-item:nth-child(3) .index {
  color: red;
}
li.musiclist-item .info {
  display: flex;
  flex: 1;
  align-items: center;
  border-bottom: 1px solid #ccc;
  padding: 10px 0;
  padding-right: 10px;
  margin-left: 10px;
}
li.musiclist-item .info .detail {
  flex: 1;
}
li.musiclist-item .info .detail h3 {
  color: #333;
  font-size: 17px;
}
li.musiclist-item .info .detail p {
  color: #888;
  font-size: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
li.musiclist-item .info .detail p .artist::after {
  content: "/";
}
li.musiclist-item .info .detail p .artist:last-of-type::after {
  content: "";
}
li.musiclist-item .info .detail p em {
  font-style: normal;
}
li.musiclist-item .info .icon {
  width: 22px;
  height: 22px;
  background-image: url("../assets/index_icon_2x.png");
  background-size: 166px 89px;
  background-position: -24px 0;
}
