.playbar {
  width: 100%;
  height: 50px;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  padding: 5px 10px;
  align-items: center;
  box-shadow: 0 -1px 3px 0 rgba(0, 0, 0, 0.15);
}
.playbar img {
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
.playbar h5 {
  flex: 1;
  padding: 0 10px;
}
.playbar .progress {
  position: relative;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playbar .progress span.play-icon {
  width: 10px;
  height: 10px;
  overflow: hidden;
}
.playbar .progress span.play-icon::before {
  content: "";
  width: 20px;
  height: 10px;
  display: block;
  border-top: 5px solid transparent;
  border-right: 12px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 8px solid green;
  margin-left: 3px;
}
.playbar .progress span.paused-icon {
  width: 10px;
  height: 10px;
  overflow: hidden;
}
.playbar .progress span.paused-icon::before {
  content: "";
  width: 20px;
  height: 10px;
  display: block;
  border-left: 8px solid red;
  margin-left: 3px;
}
.playbar .progress canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.playbar .currentlist img {
  width: 30px;
  height: 25px;
  margin-left: 10px;
}
